import angular from 'angular';
import uiRouter from 'angular-ui-router';
import template from './index.html';

let module = angular.module('hz.dashboard.project.datalist', [
  uiRouter
])
  .config(($stateProvider, $urlRouterProvider) => {
    "ngInject";

    $urlRouterProvider.otherwise('/');

    $stateProvider
      .state('hzGrid', {
        url: '/hzGrid',
        //component: 'hzGridComp'
        template: template,
      });
  })

  //.component('hzGridComp', component)
  .controller('datalistCtrl', datalistCtrl)
  .name;


datalistCtrl.$inject = [
  '$scope',
  //'horizon.framework.widgets.modal.service',
  'horizon.framework.util.http.service',
  'horizon.framework.widgets.toast.service',
  'partedModalAction'
];

function datalistCtrl($scope,
                      //smodal,
                      apiService,
                      toastService,
                      partedModalAction) {
  var self = this;

  //context
  $scope.context = {
    header: {
      id: gettext('Instance ID'),
      name: gettext('Name'),
      image: gettext('Image'),
      addresses: gettext('Private IP'),
      publicAddresses: gettext('Floating IP'),
      flavor: gettext('Flavor'),
      status: gettext('Status'),
      task: gettext('Task'),
      created: gettext('Create Time')
    },
    body: {
      nodata: gettext('No Data'),
      CPU: gettext('CPU'),
      RAM: gettext('RAM'),
      Disk: gettext('Disk'),
      Resize: gettext('Resize'),
      Confirm: gettext('Confirm'),
      Revert: gettext('Revert'),
      Monitor: gettext('Monitor')
    },
    error: {
      api: gettext('Unable to retrieve instances'),
      priviledge: gettext('Insufficient privilege level to view instance information.')
    },
    button: {
      CreateInstance: gettext('Create Instance'),
      Start: gettext('Start'),
      VNCConsole: gettext('VNC Console'),
      Shutoff: gettext('Shutoff'),
      DeleteInstances: gettext('Delete Instances'),
      More: gettext('More'),
      Edit: gettext('Edit'),
      CreateSnapshot: gettext('Create Snapshot'),
      Resize: gettext('Resize'),
      Suspend: gettext('Suspend'),
      Unsuspend: gettext('Unsuspend'),
      Pause: gettext('Pause'),
      Resume: gettext('Resume'),
      SoftReboot: gettext('Soft Reboot'),
      AttachVolume: gettext('Attach Volume'),
      DetachVolume: gettext('Detach Volume'),
      AssociateFloatingIP: gettext('Associate Floating IP'),
      DisassociateFloatingIP: gettext('Disassociate Floating IP'),
      AssociateNetwork: gettext('Associate Network'),
      DisassociateNetwork: gettext('Disassociate Network'),
      EditSecurityGroup: gettext('Edit Security Group'),
      RebuildInstance: gettext('Rebuild Instance'),
      EditMetadata: gettext('Edit Metadata'),
      quotaExceeded: gettext('(quota exceeded)'),
      noEffectivePrice: gettext('(no effective price)'),
      LockInstance: gettext('Lock'),
      UnlockInstance: gettext('Unlock'),
      SomeModal: gettext('SomeModal')
    }
  };

  //configs
  $scope.hzGridOptions = {
    realPagination: true,
    columnDefs: [
      {name: 'name'},
      {name: 'gender'},
      {name: 'company'},
      {name: 'widgets'},
      {
        name: 'cumulativeWidgets',
        field: 'widgets'
      }
    ],
    onRegisterApi: function (gridApi) {
      $scope.gridApi = gridApi
    },
    countApi: {
      url: '/data/hz-grid-count.json'
    },
    listApi: {
      url: '/data/hz-grid.json'
    },
    add: {
      templateUrl: 'edit.html',
      controller: function ($scope, $modalInstance, gridApi) {
        $scope.item = {};
        $scope.ok = function () {
          $modalInstance.close();
        }
      }
    },
    edit: {
      templateUrl: 'edit.html',
      controller: function ($scope, $modalInstance, gridApi) {
        $scope.item = gridApi.selection.getSelectedRows()[0];
        $scope.ok = function () {
          //do save
          $modalInstance.close();
        }
      }
    },
    detail: {
      templateUrl: 'detail.html',
      controller: function ($scope, $modalInstance, gridApi) {
        $scope.item = gridApi.selection.getSelectedRows()[0];
      }
    },
    delete: function (gridApi) {
      var selected = gridApi.selection.getSelectedRows()
      smodal.modal({
        title: gettext('Delete Instance'),
        tips: gettext('Please confirm your selection. This action cannot be undone.'),
        body: interpolate(gettext('The amount of instances these will be deleted is : %s'), [selected.length]),
        submit: gettext('Delete Instances'),
        name: selected,
        imgOwner: "instance"
      }).result.then(function () {
        //do something
        gridApi.core.refreshRows();
      });
    }

  };

  //actions
  $scope.actions = {
    edit: function () {
      $scope.gridApi.edit()
    },
    someModal: function () {
      $scope.gridApi.openModal({
        templateUrl: 'someModal.html',
        controller: function ($scope, $modalInstance, gridApi) {
          $scope.items = gridApi.selection.getSelectedRows();
        }
      })
    },
    partedModal: new partedModalAction($scope)
  };

}

export default module;



