(function () {
  'use strict';

  angular
    .module('horizon.framework.util.http', [])
    .service('horizon.framework.util.http.service', ApiService);

  ApiService.$inject = ['$http', '$q'];

  function ApiService($http, $q) {
    var canceller = $q.defer();

    var httpCall = function (method, url, data, config) {
      var webroot = window.WEBROOT;
      if( webroot.charAt(webroot.length-1) == '/'){
        webroot = webroot.substring(0, webroot.length-1);
      }
      url = (webroot || '') + url;
      if (!angular.isDefined(config)) {
        config = {};
      }
      // url and method are always provided
      config.method = method;
      config.url = url;
      if (angular.isDefined(data)) {
        config.data = data;
      }

      // add this for cancel request
      config.timeout = canceller.promise;
      return $http(config);
    };

    this.get = function(url, config) {
      return httpCall('GET', url, undefined, config);
    };

    this.post = function(url, data, config) {
      return httpCall('POST', url, data, config);
    };

    this.put = function(url, data, config) {
      return httpCall('PUT', url, data, config);
    };

    this.patch = function(url, data, config) {
      return httpCall('PATCH', url, data, config);
    };
    this.http = function(config){
      var url = config.url;
      var data  = config.data;
      if((config.method===void(0) || config.method.toUpperCase()=='GET')&&config.data){
        var parameters = [];
        for(var p in config.data){
          if(url.indexOf(p+'=')>-1){
            url = url.replace(new RegExp('([?&])'+p+'\=[^?&]*'),function($0,$1){
              return $1+p+'='+config.data[p]
            })
          }else{
            parameters.push(p+"="+config.data[p]);
          }
        }
        if(parameters.length){
          url += config.url.indexOf('?')>-1?'&':'?'
          url += parameters.join('&');
        }
        data = null;
      }
      return httpCall(config.method||'GET',url,data,config);
    }

    // NOTE the deviation from $http.delete which does not have the data param
    this.delete = function (url, data, config) {
      return httpCall('DELETE', url, data, config);
    };
    // cancel the request
    this.cancelRequest = function(reason){
        canceller.resolve(reason);
    };

  }
}());



