
angular.module('hz.dashboard.project.datalist')
.factory('partedModalAction',[function(){
    return function($scope){
        return function(){
            $scope.gridApi && $scope.gridApi.openModal({
                templateUrl: './data/modal.parted.html',
                controller: function ($scope, $modalInstance, gridApi) {
                    $scope.items = gridApi.selection.getSelectedRows();
                }
            })
        }

    }
}])



