import angular from 'angular';
import uiRouter from 'angular-ui-router';
import modalApi from './modalApi.component';

let aboutModule = angular.module('modalApi', [
  uiRouter
])

.config(($stateProvider) => {
  "ngInject";
  $stateProvider
    .state('hz-modal', {
      url: '/hz-modal',
      component: 'modalApi'
    });
})

.component('modalApi', modalApi)

.name;

export default aboutModule;



